/* 城市选项*/
.citypic-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 13rem;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.citypic-wrap:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 15;
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
}
.citypic-wrap .item-wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  overflow-y: auto;
}
.citypic-wrap .item-wrap ul {
  padding: 0;
}
.citypic-wrap .item-wrap li {
  height: 2.2rem;
  padding-left: .75rem;
  line-height: 2.2rem;
  color: #000;
  font-size: .75rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.citypic-wrap .item-wrap:first-child li {
  background-color: #f7f7f7;
}
.citypic-wrap .item-wrap:first-child li.active {
  background-color: #ffff;
}
.citypic-wrap .item-wrap:nth-child(2) ul {
  padding-left: .75rem;
}
.citypic-wrap .item-wrap:nth-child(2) li {
  padding-left: 0;
  position: relative;
}
.citypic-wrap .item-wrap:nth-child(2) li:after {
  content: '';
  position: absolute;
  left: 0;
  top: auto;
  bottom: 0;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 15;
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
}
.citypic-wrap .item-wrap:nth-child(2) li.active {
  color: #1890FF;
}
.citypic-wrap.hide {
  display: none;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
  .citypic-wrap:before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
  }
}
